.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 6vmin);
  font-weight: bold;
  color: white;
}

.name-input {
  background-color: transparent;
  color: white;
  font-size: calc(10px + 6vmin);
  font-weight: bold;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  outline: none;
}
.name-input:hover {
  border: 1px solid rgba(255, 255, 255, 0.3);
}
.name-input:focus {
  border: 1px solid rgba(255, 255, 255, 0.3);
}
